import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Button } from 'reactstrap';

import axios from 'axios';

export default class Weather extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      weatherData: {
        city: {
          name: '',
          country: ''
        },
        list: []
      },
      fiveDayForecast: []
    };

    this.subscription = null;
  }

  componentWillMount() {
    this.getLocation();
  }

  getLocation() {
    if (navigator) {
      const geolocation = navigator.geolocation;

      new Promise((resolve, reject) => {
        geolocation.getCurrentPosition((position) => {
          this.getWeatherForecast(position.coords.latitude, position.coords.longitude);
        }, () => {
          reject (new Error('Permission denied'));
        });
      });
    }
  }

  getWeatherForecast(lat, long) {
    const API_KEY = `46ddd9c3c6a545d0d62e60754768e38d`;
    const WEATHER_URL = `http://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${long}&appid=${API_KEY}&units=imperial`;

    return axios.get(WEATHER_URL).then(
      (res) => {
        if (res.data.cod !== '200' && res.data.message){
          throw new Error(res.data.message);
        } else {
          let fiveDayForecast = [];
          let dayArray = [];
          res.data.list.map((timeData, index) => {
            dayArray.push(timeData);

            if ((index + 1) % 8 === 0) {
              fiveDayForecast.push(dayArray);
              dayArray = new Array;
            }
          });
          this.setState({
            weatherData: res.data,
            fiveDayForecast: fiveDayForecast
          });
          return res.data;
        }
      },
      (res) => {
        throw new Error(res.data.message);
      }
    );
  }

  render() {
    const { loading } = this.props;
    console.log(this.state.fiveDayForecast);
    return (
      <div className="text-center mt-4 mb-4">
        { this.state.weatherData.city.name }, { this.state.weatherData.city.country }
        {
          this.state.fiveDayForecast.map(function(day) {
            day.map(function(hour) {
              console.log(hour);
                return [
                  <div key={hour.dt}>
                    Time { hour.dt_txt }
                    Current { hour.main.temp }
                    Hi { hour.main.temp_max }
                    Low { hour.main.temp_min }
                  </div>
                ]
            })
          })
       }
      </div>
    );
  }
}



